import type { Meta, StoryObj } from '@storybook/web-components';
import { html } from 'lit';

const meta: Meta = {
  title: 'Charts/LineChart',
  component: 'line-chart',
  tags: ['autodocs'],
  parameters: {
    docs: {
      description: {
        component: 'A simple line chart using ApexCharts inside a Stencil component. It supports custom data and category labels.',
      },
    },
  },
  argTypes: {
    data: {
      control: 'object',
      description: 'Data points to plot',
      defaultValue: [10, 20, 30, 40, 50],
    },
    categories: {
      control: 'object',
      description: 'X-axis labels',
      defaultValue: ['Jan', 'Feb', 'Mar', 'Apr', 'May'],
    },
  },
};

export default meta;

// Default story with interactive controls
export const Default: StoryObj = {
  args: {
    data: [5, 10, 15],
    categories: ['A', 'B', 'C'],
  },
  render: (args) => html`
    <line-chart .data=${args.data} .categories=${args.categories}></line-chart>
  `,
};

// More data points
export const ManyPoints: StoryObj = {
  args: {
    data: [12, 25, 40, 30, 22, 50, 65],
    categories: ['Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat', 'Sun'],
  },
  render: (args) => html`
    <line-chart .data=${args.data} .categories=${args.categories}></line-chart>
  `,
};

// Empty chart
export const Empty: StoryObj = {
  args: {
    data: [],
    categories: [],
  },
  render: (args) => html`
    <line-chart .data=${args.data} .categories=${args.categories}></line-chart>
  `,
};
